.material-icons {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
}

.icon-left {
    margin-right: 0.75rem;
}

.sidenav {
    background-color: #64b5f6;
    color: white;
}

.sidenav-trigger {
    background-color: transparent;
    border: none; 
    box-shadow: none;
    color: white;
}

.sidenav-trigger:active {
    background-color: transparent;
    border: none; 
    box-shadow: none;
    color: white;
}

.sidenav li a {
    color:white;
    font-size: 1.5rem;
    margin: 0.75rem 0;
}

.narrowList {
    margin: 0 20%;
}

@media only screen and (max-width: 500px) {
    nav .brand-logo {
        font-size: 1.10rem;
    }
    
}

@media only screen and (max-width: 990px) {
    div.portfolio-info {
        text-align: center;
    }

    .reverse {
        display: flex;
        flex-direction: column-reverse;
    }
    
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
